// пятна грязи

.dirt-group {
  position: absolute;
  height: auto;
  mix-blend-mode: multiply;

  img {
    width: 100%;
    max-width: 100%;
  }
}

.dirt-group--1 {
  left: 2%;
  top: 10%;
  width: 29.3%;

  @media (max-aspect-ratio: 16/9.01) {
    width: 39.4%;
    left: 1%;
  }
}

.dirt-group--2 {
  left: 29.5%;
  top: 51.5%;
  width: 10%;

  mix-blend-mode: normal;

  @media (max-aspect-ratio: 16/9.01) {
    width: 13.48%;
    left: 39.5%;
  }
}

.dirt-group--3 {
  left: 31.2%;
  top: 25%;
  width: 30.9%;

  @media (max-aspect-ratio: 16/9.01) {
    left: 42%;
    width: 38.6%;
  }
}

.dirt-group--4 {
  top: 0%;
  width: 46.66%;
  left: 42.3%;

  @media (max-aspect-ratio: 16/9.01) {
    width: 62.31%;
    left: 56.2%;
  }
}

.dirt-group--5 {
  top: 27%;
  width: 11.81%;
  left: 88%;

  @media (max-aspect-ratio: 16/9.01) {
    width: 15.8%;
    left: 117%;
  }
}

.dirt-group--6 {
  top: 1.5%;
  width: 15.2%;
  left: 103.3%;

  @media (max-aspect-ratio: 16/9.01) {
    width: 20.31%;
    left: 138%;
  }
}

.dirt-group--7 {
