// пена

.foam {
  position: absolute;
  z-index: 3;
  scale: 0;

  img {
    width: 100%;
    max-width: 100%;
  }
}

.foam--1 {
  top: 3%;
  left: 0%;
  width: 32.76%;
  opacity: 1;

  @media (max-aspect-ratio: 16/9.01) {
    width: 42.36%;
  }
}

.foam--2 {
  top: 40%;
  left: 26%;
  width: 23.5%;

  @media (max-aspect-ratio: 16/9.01) {
    top: 30%;
    left: 35%;
    width: 31.3%;
  }
}

.foam--3 {
  top: 0%;
  left: 34%;
  width: 28.13%;

  @media (max-aspect-ratio: 16/9.01) {
    top: 0%;
    left: 35%;
    width: 36.5%;
  }
}

.foam--4 {
  top: -8%;
  left: 43%;
  width: 42.04%;

  @media (max-aspect-ratio: 16/9.01) {
    top: -9%;
    left: 54%;
    width: 55.84%;
  }
}

.foam--5 {
  top: 12%;
  left: 84%;
  width: 17.23%;

  @media (max-aspect-ratio: 16/9.01) {
    top: 6%;
    left: 113%;
    width: 22.3%;
  }
}

.foam--6 {
  width: 25.84%;
  top: 1%;
  left: 102%;

  @media (max-aspect-ratio: 16/9.01) {
    top: 6%;
    left: 139%;
    width: 23.8%;
  }
}

.foam--7 {
  top: 9.5%;
  left: 125.5%;
  width: 19.86%;
  transform: scaleX(0.61);

  @media (max-aspect-ratio: 16/9.01) {
    top: 7.5%;
    left: 167.5%;
    width: 25.8%;
  }
}

.foam--top {
  width: 100%;
  top: -5%;
  transform: translate(0, -100%);
  left: 0;
  z-index: 2;
  scale: 1;

  @media (max-aspect-ratio: 16/9.01) {
    top: 0;
  }
}
