.banner {
  display: flex;
  width: 100%;
  height: 100vh;
  align-items: center;
  justify-content: center;
}

.banner__wrapper {
  overflow: hidden;
  position: relative;
  margin: 0 auto;
  border-radius: 30px;
  max-width: 100vw;

  @media (min-aspect-ratio: 16/9) {
    width: 177.77vh;
    height: 100vh;
  }

  @media (max-aspect-ratio: 16/9.01) and (min-aspect-ratio: 4/3) {
    width: 177.77vh;
    height: 100vh;

    width: 133.33vh;
    height: 100vh;
  }

  @media (max-aspect-ratio: 4/3) {
    width: 100vw;
    height: 75vw;
  }
}
