// бактерии они же микробы

.bacteria {
  width: 21.9%;
  position: absolute;
  background-color: transparent;
  border: none;
  z-index: 2;
  opacity: 0;
  scale: 0;

  img {
    width: 100%;
  }

  @media (max-aspect-ratio: 16/9.01) {
    width: 25.7%;
  }
}

.bacteria--1 {
  top: 50%;
  transform: translateY(-50%);
  left: 0%;
  scale: 1;

  @media (max-aspect-ratio: 16/9.01) {
    left: 2%;
  }
}

.bacteria--2 {
  top: 40%;
  left: 14%;

  @media (max-aspect-ratio: 16/9.01) {
    left: 20%;
  }
}

.bacteria--3 {
  top: 0%;
  left: 29%;

  @media (max-aspect-ratio: 16/9.01) {
    left: 43%;
  }
}

.bacteria--4 {
  top: 16.5%;
  left: 60%;
  width: 14.7%;

  @media (max-aspect-ratio: 16/9.01) {
    left: 80.5%;
    width: 19.7%;
  }
}

.bacteria--5 {
  top: 28%;
  left: 76%;

  @media (max-aspect-ratio: 16/9.01) {
    left: 103%;
  }
}

.bacteria--6 {
  width: 25%;
  top: 0%;
  left: 101%;
  clip-path: polygon(0 0, 100% 0, 100% 70%, 0 70%);

  @media (max-aspect-ratio: 16/9.01) {
    width: 20.42%;
    left: 140%;
  }
}

.bacteria--7 {
  top: 60%;
  left: 120%;

  @media (max-aspect-ratio: 16/9.01) {
    left: 160%;
  }
}

.bacteria--start-1 {
  top: 49%;
  right: 0%;
  width: 32%;
  transform: rotateZ(25deg);
  clip-path: polygon(0 0, 100% 0, 100% 41%, 0 84%);

  opacity: 0;
  scale: 1;

  @media (max-aspect-ratio: 16/9.01) {
    width: 32%;
  }
}

.bacteria--start-2 {
  top: 30.3%;
  right: 18.2%;
  width: 25.8%;
  transform: rotateZ(13deg);

  opacity: 0;
  scale: 0;

  @media (max-aspect-ratio: 16/9.01) {
    width: 25.8%;
  }
}

.bacteria--start-3 {
  top: 11.7%;
  right: -5.5%;
  transform: rotateZ(-22deg);
  width: 33%;
  opacity: 0;
  scale: 0;

  @media (max-aspect-ratio: 16/9.01) {
    width: 33%;
  }
}
