.start-banner {
  z-index: 1;
  opacity: 1;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  position: absolute;

  .btn {
    margin-top: 4.55%;
  }

  img {
    width: 100%;
  }
}

.start-banner__background {
  object-fit: cover;
  width: 100%;
  height: 100%;


  @media (min-aspect-ratio: 16/9) {
    aspect-ratio: 16 / 9;
  }

  @media (max-aspect-ratio: 16/9) and (min-aspect-ratio: 4/3) {
    aspect-ratio: 1 / 1;
  }

  @media (max-aspect-ratio: 4/3) {
    aspect-ratio: 4 / 3;
  }
}

.start-banner__gradient {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background: linear-gradient(90deg, rgba(0, 104, 34, 1) 0%, rgba(0, 104, 34, 0) 100%);
  z-index: 2;
  opacity: 0;
}

.start-banner__text {
  position: absolute;
  z-index: 3;
  width: 100%;
  top: 49.5%;
  left: 4.6%;
  transform: translateY(-50.5%);
  color: #ffffff;
  text-transform: uppercase;
  font-weight: 700;
  opacity: 0;

  p {
    display: flex;
    flex-direction: column;
    margin: 0;
  }
}

.start-banner__row {
  display: block;
  line-height: 106%;
}

.start-banner__row--1 {
  font-size: 2.55rem;
  letter-spacing: 0.04em;
}

.start-banner__row--2 {
  font-size: 2.75rem;
  line-height: 1.34em;
  letter-spacing: 0.03em;
  background-color: #00402B;
  width: fit-content;
  margin-bottom: 3.35%;

  border-left: 0.3em solid #00402b;
  border-right: 0.3em solid #00402b;
  margin-left: -0.3em;
}

.start-banner__row--3 {
  font-size: 2.2rem;
  font-weight: 500;
  text-transform: none;
}
